.gallery-imgs {
    column-count: 3;
    max-width: 1000px;
    margin: 0 auto;
    padding: 30px;
    box-shadow: 0 1px 10px black;
}

.gallery-img {
    width: 100%;
    height: auto;
    display: block;
    box-sizing: border-box;
    margin: 5px;
    border: 1px solid rgb(53, 53, 53);
}
